.form-input {
  display: grid;
  grid-gap: 6.5px;

  & > * {
      padding-left: 20px;
  }

  &__label {
      font-size: 14px;
  }
  &__input {
      box-sizing: border-box;
      height: 50px;
      width: 426px;
      border: 1px solid #E6E6E6;
      border-radius: 5px;
      color: #333333;
      font-size: 18px;
  }
}

.form-button {
  &__button {
      height: 50px;
      width: 243px;
      border-radius: 50px;
      background-color: $green;

      font-size: 16px;
      color: white;

      &:focus {
          outline: none;
      }
  }
} 